<template>
	<div class="emb-product-add">
		<v-row>
			<v-col cols="12" sm="9" md="10" lg="10" class="mx-auto">
				<v-row>
					<v-col cols="12" sm="12" md="6" lg="6" class="content-wrap pl-md-6 emb-card">
						<a class="pt-4 d-block font-weight-medium">set currency equivalency
						</a>
                        <div class="setCurency">
                            <div class="ml-5">
                               <v-text-field  prepend-icon="add" v-model="USD" class="price-input" placeholder="Equivalent To $1">
						</v-text-field>
                            </div>
                        </div>
						
						<v-btn color="accent" @click="updateCurrency" class="mx-3 mt-10" large>Add Currency</v-btn>
						<!-- <v-btn color="white" large>Discard</v-btn> -->
					</v-col>
				</v-row>
			</v-col>
		</v-row>
	</div>
</template>

<script>
	import currency from "Api/currency";
// import contactVue from './contact.vue';
	export default {
		data() {
			return {
				USD: '',
				currencyName: '',
				symbol:''
			}
		},
		methods: {
            async updateCurrency() {
                try {
                    const currentValue= this.USD
                    const currencyId= this.$route.params.currency
                    console.log(currencyId)
                    const res = await currency.updatecurrency(currentValue, currencyId)
                    console.log(res)
                } catch (err) {
                    console.log(err)
                }
            }
        },
        async mounted(){
            try {
                console.log(this.$route.params.currency)
                const res = await currency.getcurrency()
                res.data.data.forEach(el => {
                    if (el._id === this.$route.params.currency) {
                        // console.log('hello', el.currentValue)
                        this.USD= el.currentValue
                    }
                });
            } catch (err) {
                console.log(err)
            }
        }
	}
</script>
<style  scoped>
.setCurrency{
    display: flex;
    /* align-items: center; */
    justify-content: center;
}
.emb-product-add{
    /* margin-top: 10rem; */
    /* margin-left: 20rem; */
}
</style>