import api from "./index";

export default {
    async PostCurrency(data) {
        const res = await api().post(`/api/currencies`, data)
        return res
    },
    async updatecurrency(currentValue, currencyId) {
        const res = await api().post(`/api/currencies/${currencyId}`, currentValue)
        return res
    },
    async getcurrency() {
        const res = await api().get(`/api/currencies`)
        return res
    },
}